// 配置路由
import VueRouter from "vue-router";
import Vue from 'vue'
// 引入路由
import Home from './pages/home'
import Details from './pages/details'
import Login from './pages/login'
import Massage from './pages/details/message'
import Journalism from './pages/details/journalism'
import Consult from './pages/details/consult'
import Apply from './pages/apply'
import Fill from './pages/apply/fill'
import Affirm from './pages/apply/affirm'
import Pay from './pages/apply/pay'
import Verify from './pages/apply/verify'
import Zhezhao from './pages/apply/zhezhao'
import Announcement from './pages/announcement'
import Agreement from './pages/agreement'
import Protocol from './pages/agreement/protocol'
import Policy from './pages/agreement/policy'
import Matter from './pages/agreement/matter'
import Message from './pages/message'
import Info from './pages/message/info'
import Subject from './pages/message/subject'
import Competition from './pages/message/subject/competition'
import Feedback from './pages/message/subject/feedback'
import Order from './pages/message/subject/order'
import Honor from './pages/message/subject/honor'
import Subject1 from './pages/message/subject/subject1'
import Answer from './pages/answer'

Vue.use(VueRouter)

let router = new VueRouter({
  // 配置路由
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/details',
      name: 'Details',
      component: Details,
      // 二级路由
      children: [
        {
          path: 'matchMassage',
          name: 'MatchMassage',
          component: Massage
        },
        {
          path: 'journalism',
          name: 'Journalism',
          component: Journalism,
          // children: [

          // ]
        },
        {
          path: 'consult',
          name: 'Consult',
          component: Consult
        },
        {
          path: '/details',
          redirect: '/details/matchMassage'
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/apply',
      name: 'Apply',
      component: Apply,
      children: [
        {
          path: 'fill',
          name: 'Fill',
          component: Fill
        },
        {
          path: 'affirm',
          name: 'Affirm',
          component: Affirm
        },
        {
          path: 'pay',
          name: 'Pay',
          component: Pay
        },
        {
          path: 'verify',
          name: 'Verify',
          component: Verify,
          children: [
            {
              path: 'zhezhao',
              name: 'Zhezhao',
              component: Zhezhao
            },
          ]
        },

        {
          path: '/apply',
          redirect: '/apply/fill'
        }
      ]
    },
    {
      path: '/announcement/:content?',
      name: 'Announcement',
      component: Announcement
    },
    {
      path: '/agreement',
      name: 'Agreement',
      component: Agreement,
      children: [
        {
          path: 'protocol',
          name: 'Protocol',
          component: Protocol
        },
        {
          path: 'policy',
          name: 'Policy',
          component: Policy
        },
        {
          path: 'matter',
          name: 'Matter',
          component: Matter
        },
        {
          path: '/agreement',
          redirect: '/agreement/protocol'
        }
      ]
    },
    {
      path: '/message',
      name: 'Message',
      component: Message,
      children: [
        {
          path: 'info',
          name: 'Info',
          component: Info
        },
        {
          path: 'subject',
          name: 'Subject',
          component: Subject,
          children: [
            {
              path: 'competition',
              name: 'Competition',
              component: Competition
            },
            {
              path: 'feedback',
              name: 'Feedback',
              component: Feedback
            },
            {
              path: 'order',
              name: 'Order',
              component: Order
            },
            {
              path: 'honor',
              name: 'Honor',
              component: Honor
            },
            {
              path: 'subject1',
              name: 'Subject1',
              component: Subject1
            },
            {
              path: '/message/subject',
              redirect: '/message/subject/subject1'
            }
          ]
        },

        {
          path: '/message',
          redirect: '/message/info'
        }
      ]
    },
    {
      path: '/answer',
      name: 'Answer',
      component: Answer
    },
    {
      // 重定向
      path: '*',
      redirect: '/home'
    }
  ],
  // 当切换到新路由时，想要页面滚到顶部
  // 滚动行为
  scrollBehavior(to, from, savedPosition) {
    // { x:0,y: 0 }  滚动到新路由后 页面x，y轴 在最上方s
    // y: 0 代表滚动轴在最上方
    return { y: 0 }
  }
})

export default router