<template>
  <div class="login">
    <Header />
    <div class="zy-login">
      <div class="zy-login-center">
        <div class="zy-login-center-case">
          <div class="zy-login-center-case-way">
            <div class="case-way case-thick">手机号登录</div>
            <div class="case-way">第三方软件登录</div>
            <div class="case-way-input">
              <div style="margin-top: 15px">
                <el-input
                  placeholder="请输入手机号"
                  v-model="input1"
                  class="input-with-select"
                >
                  <el-select
                    v-model="select"
                    slot="prepend"
                    placeholder="中国"
                    style="padding: 0px"
                  >
                    <el-option label="中国+86" value=""></el-option>
                  </el-select>
                </el-input>
              </div>
              <div class="case-way-input-verify">
                <el-input
                  v-model="input2"
                  placeholder="输入右侧图形验证码"
                ></el-input>
                <span>{{ strCode }}</span>
              </div>
              <div class="case-way-input-note">
                <el-input
                  v-model="textCode"
                  placeholder="输入短信验证码"
                ></el-input>
                <el-button @click="textRandom()">默认按钮</el-button>
              </div>
              <div class="case-way-input-button">
                <el-button plain @click="open1">登录/注册</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="cy-home-about">
        <div class="cy-home-about-buttom">
          <p>中国对外翻译有限公司 版权所有</p>
          <p>京ICP备13006088号-1</p>
          <p>京公网安备11010702001693号</p>
          <p>广播电视节目制作经营许可证：（京）字第10929号</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      input1: "",
      input2: "",
      // input3: "",
      select: "中国+86",
      strCode: "",
      textCode: "",
      loginTitle: "123",
    };
  },
  mounted() {
    this.fRandom();
    this.textRandom();
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    open1() {
      if (this.input1 == 12366666666 && this.strCode == this.input2) {
        const h = this.$createElement;
        this.$notify({
          title: this.loginTitle,
          message: h("i", { style: "color: teal" }, "登陆成功"),
        });
        this.$router.push({ path: "/home" });
      } else {
        const h = this.$createElement;
        this.$notify({
          title: "手机号或验证码输入错误",
          message: h("i", { style: "color: teal" }, "登陆失败"),
        });
      }
    },
    // 生成验证码的函数
    fRandom() {
      const fRandom = Math.random();
      this.strCode = Math.floor(fRandom * 9000) + 1000;
    },
    // 生成短信验证码
    textRandom() {
      const fRandom = Math.random();
      this.textCode = Math.floor(fRandom * 900000) + 100000;
    },
  },
};
</script>

