  },
  // 一个注释
  methods: {
    packUp() {
      this.rightNav = this.rightNav === 1 ? 0 : 1;
    },
    isHoverOver(event) {
      if (event.target.classList.contains("xianshi")) {
        this.isHover = event.target.classList[0];
      }
    },
    isHoverLeave() {
      this.isHover = false;
    },
    modalityAlter() {
      this.modality = this.modality == 0 ? 1 : 0;
    },
    colorChange(event) {
      if (event.target.tagName === "LI") {
        if (event.target.classList.value == 1) {
          this.colorC = "rgba(240, 245, 255, 1)";
        } else if (event.target.classList.value == 2) {
          this.colorC = "rgba(152, 191, 173, 1)";
        } else if (event.target.classList.value == 3) {
          this.colorC = "rgba(218, 205, 201, 1)";
        } else if (event.target.classList.value == 4) {
          this.colorC = "rgba(0, 0, 0, 1)";
        } else if (event.target.classList.value == 5) {
          this.colorC = "rgba(127, 143, 175, 1)";
        } else if (event.target.classList.value == 6) {
          this.colorC = "rgba(209, 213, 185, 1)";
        }
      }
    },
    colorDisplay() {
      this.colorShow = this.colorShow == 0 ? 1 : 0;
      console.log(this.colorShow);
    },
    goHome() {
      this.$router.push({
        path: "/home",
