<script>
import { mapGetters } from "vuex";

export default {
  name: "Details",
  data() {
    return {
      finishTime: "",
      future: +new Date("2024-11-30 23:59:59"),
      total: "",
      countTimer: null,
    };
  },
  mounted() {
    // this.$store.dispatch("details/getNewList");
    // sessionStorage.setItem("NEWLISTS", JSON.stringify(this.newLists));
    this.$store.dispatch("details/getTimes");

    // 强制Vue重新计算计算属性
    // this.intervalld = setInterval(() => {
    //   this.$forceUpdate();
    // }, 100);

    this.changeTime();
    this.countTimer = setInterval(() => {
      this.changeTime();
      // console.log(111);
    }, 1000);
  },
  // 组件销毁前 清除定时器
  beforeDestroy() {
    if (this.countTimer) {
      clearInterval(this.countTimer);
      this.countTimer = null;
    }
  },
  methods: {
    changeTime() {
      const now = Date.now();
      this.total = parseInt(this.future - now) / 1000;
      this.finishTime = this.finishTimes;
    },
    goApply() {
      this.$router.push({
        path: "/apply",
      });
    },
  },
  computed: {
    // ...mapGetters("details", ["newLists"]),
    ...mapGetters("details", ["gameTimes"]),
    finishTimes() {
      // 时间戳转换时分秒公式
      // (将来的时间戳 - 现在的时间戳) / 1000  得到总的秒数
      // d=parseInt(总秒数/60/60/24)  计算天数
      // h=parseInt(总秒数/60/60%24)  计算小时
      // m=parseInt(总秒数/60%60)  计算分数
      // s=parseInt(总秒数%60)  计算当前秒数
      // parseInt()  只保留证整数
      // const date = new Date();

      // Date.now() 方法 只能得到当前的时间戳
      // console.log("现在", Date.now());
      // const now = Date.now();

      // 得到指定时间的时间戳 +new Date()
      // console.log(+new Date("2024-11-30 23:59:59"));
      // const future = ;

      // 剩余时间总秒数
      // const total = parseInt(this.future - now) / 1000;

      if (this.total < 0) {
        return "比赛已结束";
      }
      // 计算剩余天数
      let d = parseInt(this.total / 60 / 60 / 24);
      // 计算剩余小时数
      let h = parseInt((this.total / 60 / 60) % 24);
      h = h < 10 ? "0" + h : h;
      // 计算剩余分钟数
      let m = parseInt((this.total / 60) % 60);
      m = m < 10 ? "0" + m : m;
      // 计算剩余秒数
      let s = parseInt(this.total % 60);
      s = s < 10 ? "0" + s : s;

      let day = `${d}天${h}:${m}:${s}`;
      return day;
    },
  },
  // watch: {
  //   finishTime: {
  //     handler(newValue, oldValue) {
  //       this.finishTime = newValue;
  //       console.log(111111);
  //     },
  //   },
  // },
};
</script>

