  <div class="zy-agreement-content-r-word">
    <h2>隐私政策</h2>
    <div class="zy-agreement-content-title"><span>隐私政策</span></div>
    <h3>一、引言</h3>
    <p>
      中国对外翻译有限公司（以下称“我们”）尊重并保护所有使用我们提供的服务的用户的隐私权，在您使用我们提供的服务时，我们可能会收集和使用您的相关信息，我们希望通过本《隐私政策》向您说明：我们收集您的个人信息的方式和范围、我们使用您的个人信息的方式和范围，以及您的个人信息的保护。本《隐私政策》与您使用我们提供的服务息息相关，因此我们建议您完整地阅读，以帮助您更好地保护您的隐私。如您对本《隐私政策》有任何疑问，您可以通过我们平台公布的联系方式与我们联系。如果您不同意本《隐私政策》任何内容，您应立即停止使用我方平台服务。当您使用我们提供的任一服务时，即表示您已同意我们按照本《隐私政策》来合法收集、使用和保护您的个人信息。
      除非本《隐私政策》特别说明，本《隐私政策》的条款中使用的定义与《用户协议》中的定义具有相同含义。
    </p>
    <p>二、适用范围</p>
    <p>
      本《隐私政策》适用于我方平台服务。您访问我们平台网站及/或登录相关客户端使用我方平台服务，均应适用本《隐私政策》。需要特别说明的是，除另有约定（不论是明示的还是默示的）或说明，本隐私政策不适用于其他第三方向您提供的服务，例如我们平台上的第三方依托我们平台向您提供服务时，您向第三方提供的个人信息不适用于本《隐私政策》，我们对任何第三方使用由您提供的信息不承担任何责任。
    </p>
    <p>
      我们提供服务时，可能会收集、存储和使用下列与您有关的信息，收集这些信息是为了向您提供更好、更优、更个性化的服务，我们收集信息的方式如下：
    </p>
    <p>1. 您提供的信息</p>
    <p>
      当您在注册我方平台账户及/或使用我方平台服务及/或进行支付时，填写及/或提交的信息，包括您的姓名、性别、国籍、语言、职业、教育、年龄、身份证号码、电话号码、电子邮箱、地址、兴趣爱好、支付账户及相关附加信息等（如您所在的省份和城市、邮政编码等），也包括您通过我方平台服务向其他方提供的共享信息，以及您使用我方平台服务时所存储的信息。
    </p>
    <p>
      2.
      在您使用我方平台服务过程中，我方平台收集的信息。为了提供并优化您需要的服务，我们可能会收集您使用服务的相关信息，这类信息包括：
    </p>
    <p>
      1).
      在您使用我方平台服务，或访问我方平台网站时，我们自动接收并记录的您的浏览器和计算机上的信息，包括但不限于您的IP地址、浏览器的类型、使用的语言、访问日期和时间、软硬件特征信息及您需求的网页记录等数据；如您下载或使用我们或其关联公司客户端软件，或访问移动网页使用我方平台服务时，我们可能会读取与您位置和移动设备相关的信息，包括但不限于设备型号、设备识别码、操作系统、分辨率、电信运营商等；
    </p>
    <p>
      2) 日志信息，指您使用我方平台服务时，系统可能通过cookies、web
      beacon或其他方式自动采集的技术信息，来制定您的国家及使用语言等；
    </p>
    <p>
      3)
      在使用我方平台服务时搜索或浏览的信息，例如您使用的网页搜索词语、访问的社交媒体页面url地址，以及您在使用我方平台服务时浏览或要求提供的其他信息和内容详情；
    </p>
    <p>
      4)
      您通过我方平台服务进行通讯的信息，例如通讯账号，以及通讯时间、数据和时长；
    </p>
    <p>
      5)
      您通过我方平台服务分享的内容所包含的信息（元数据），例如拍摄或录像的日期、时间或地点等；以及在使用我方平台服务时提供和录制的信息，包括但不限于购买课程、预约课程、课程录音录像等。
    </p>
    <p>
      我们的cookies并不搜集您计算机浏览器或硬盘上的额外信息。大部分浏览器都有拒绝储存计算机上数据到cookies的选项，您可以修改对Cookie的接受程度或者拒绝我们的Cookie，不过我们不建议您作此设置，因这一举动在某些情况下可能会影响您安全访问我方平台网站和使用我方平台服务。
    </p>
    <h3>四、信息使用</h3>
    <p>
      1.
      因收集您的信息是为了向您提供服务及提升服务质量，为了实现这一目的，我们可能会把您的信息用于下列用途：
    </p>
    <p>1) 向您提供您使用的各项服务，并设计、维护、改进这些服务；</p>
    <p>
      2)
      在我们提供服务时，用于身份验证、客户服务、安全防范、诈骗监测、存档和备份用途，确保我们向您提供的产品和服务的安全性；
    </p>
    <p>
      3)
      根据您需求、地域、年龄、职业、语言能力、背景、兴趣、学习偏好等，匹配适合您的学习指导顾问与学习内容；
    </p>
    <p>
      4)
      分析您使用我方平台服务的情况，使我们更加了解您如何接入和使用我方平台服务，从而针对性地回应您的个性化需求，例如语言设定、位置设定、个性化帮助和指示，或对您和其他用户做出其他方面的回应；
    </p>
    <p>
      5)
      您在使用我方平台服务时，我们可能使用您的信息向您的设备发送电子邮件、新闻或推送通知。如您不希望收到这些信息，可以按照我们的相关提示，在设备上选择取消订阅；
    </p>
    <p>
      6)
      向您推荐您可能感兴趣的内容，包括但不限于向您发送产品和服务信息，或通过系统向您展示个性化的第三方推广信息，或者在征得您同意的情况下与我们的合作伙伴共享信息以便他们向您发送有关其产品和服务的信息；
    </p>
    <p>7) 软件认证或管理软件升级；</p>
    <p>8) 向您提供与您更加相关的广告以替代普遍投放的广告；</p>
    <p>
      9)
      让您参与有关我们产品和服务的调查，或者促销及抽奖活动；以及经您许可的其他用途。
    </p>
    <p>
      2.
      我们对您的信息承担保密义务，不会出售或出租您的任何信息，我们在下列情况下才会将您的信息与第三方共享：
    </p>
    <p>1) 事先获得您的同意或授权；</p>
    <p>2) 根据法律法规的规定或行政或司法机关的要求；</p>
    <p>3) 向我们的关联方分享您的个人信息；</p>
    <p>
      4)
      向可信赖的合作伙伴提供您的个人信息，让他们根据我们的指示并遵循本《隐私政策》以及其他任何相应的保密和安全措施来为我们处理这些信息；
    </p>
    <p>
      5)
      如您是适格的知识产权投诉人并已提起投诉，应被投诉人要求，向被投诉人披露，以便双方处理可能的权利纠纷；
    </p>
    <p>
      6)
      只有共享您的信息，才能提供您需要的服务，或处理您与他人的纠纷或争议。例如您在我们平台上创建的某一交易中，如交易任何一方履行或部分履行了交易义务并提出信息披露请求的，我们会视情况向该用户提供其交易对方的联络方式等必要信息，以促成交易的完成或纠纷的解决；
    </p>
    <p>
      7)
      如您出现违反中国有关法律法规或者与我们的相关协议或我们的相关规则的情况，需要向第三方披露；
    </p>
    <p>8) 查找、预防或处理欺诈、安全或技术方面的问题；</p>
    <p>
      9)
      紧急情况下，为维护用户及公众的利益；或为维护我们及其关联方或用户的合法权益。
    </p>
    <h3>五、信息保护</h3>
    <p>
      为保障您的信息安全，我们努力采取各种合理的物理、电子和管理方面的安全措施来保护您的信息，使您的信息不会被泄漏、毁损或者丢失，包括但不限于SSL、信息加密存储、数据中心的访问控制。我们对可能接触到您的信息的员工或外包人员也采取了严格管理，包括但不限于根据岗位的不同采取不同的权限控制，与他们签署保密协议，监控他们的操作情况等措施。我们会按现有技术提供相应的安全措施来保护您的信息，提供合理的安全保障，我们将尽力做到使您的信息不被泄漏、毁损或丢失。
    </p>
    <p>
      您的账户均有安全保护功能，请妥善保管您的账户及密码信息。我们将通过向其它服务器备份、对用户密码进行加密等安全措施确保您的信息不丢失，不被滥用和变造。尽管有前述安全措施，但同时也请您理解在信息网络上不存在“完善的安全措施”。在使用我方平台服务进行网上交易时，您不可避免的要向交易对方或潜在的交易对方披露自己的个人信息，如银行账户信息、联络方式或者邮政地址。请您妥善保护自己的个人信息，仅在必要的情形下向他人提供。如您发现自己的个人信息泄密，尤其是你的账户及密码发生泄露，请您立即联络我们客服，以便我们采取相应措施。
    </p>
    <h3>六、未成年人保护</h3>
    <p>
      我们鼓励父母或法定监护人（以下统称“监护人”）指导未满十八岁的未成年人使用我方平台服务。我们建议未成年人鼓励他们的监护人阅读本《隐私政策》，并建议未成年人在提交的个人信息之前寻求监护人的同意和指导。我们将建立、维护合理的程序，以保护未成年人个人资料的保密性及安全性。
    </p>
    <h3>七、变更</h3>
    <p>
      我们可能适时修订本《隐私政策》的条款，该等修订构成本《隐私政策》的一部分。如该等修订造成您在本《隐私政策》下权利的实质减少，我们将通过在平台显著位置提示或向您发送电子邮件或以其他方式通知您。在该种情况下，若您继续使用我方平台服务，即表示同意受经修订的本《隐私政策》的约束。
    </p>
