<template>
  <div id="app">
    <ckeditor
      :editor="editor"
      v-model="editorData"
      :config="editorConfig"
    ></ckeditor>
  </div>
</template>

<script>
import {
  ClassicEditor,
  Bold,
  Essentials,
  Italic,
  Mention,
  Paragraph,
  Undo,
  Link,
  List,
  Image,
  Table,
  MediaEmbed,
  BlockQuote,
  Indent,
  TodoList,
  Alignment,
  Code,
} from "ckeditor5";

import "ckeditor5/ckeditor5.css";
import "ckeditor5-premium-features/ckeditor5-premium-features.css";
import "@ckeditor/ckeditor5-build-classic/build/translations/zh-cn.js";

export default {
  name: "CkeDitor",
  data() {
    return {
      editor: ClassicEditor,
      editorData: "",
      editorConfig: {
        language: "zh-cn",
        toolbar: {
          items: [
            "undo",
            "redo",
            "|",
            "bold",
            "italic",
            "link",
            "bulletedList",
            "numberedList",
            "|",
            "outdent",
            "indent",
            "|",
            "blockQuote",
            "insertTable",
            "mediaEmbed",
            "todoList",
            "alignment",
            "code",
          ],
        },
        plugins: [
          Bold,
          Essentials,
          Italic,
          Mention,
          Paragraph,
          Undo,
          Link,
          List,
          Image,
          Table,
          MediaEmbed,
          BlockQuote,
          Indent,
          TodoList,
          Alignment,
          Code,
        ],
        mention: {
          // Mention configuration
        },
      },
    };
  },
};
</script>