const state = {
  inputHQS: {}
}

const actions = {
  getinputHQ(context, value) {
    console.log(value);
    context.commit('GETINPUTHQ', value)
  }
}

const mutations = {
  GETINPUTHQ(state, value) {
    state.inputHQS = value
  }
}

const getters = {}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}