import Vue from 'vue'
import App from './App.vue'
// 引入路由
import router from './routers'
// 引入饿了么组件
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
// 使用饿了么组件
Vue.use(ElementUI);
// 引入富文本编辑器
import CKEditor from '@ckeditor/ckeditor5-vue2';
// 引用富文本编辑器
Vue.use(CKEditor);
// 引入vuex
import store from './store';

// 引入全局组件
import Header from './components/header'
import Buttom from './components/bottom'
import Organization from './components/organization'
import NavRight from './components/nav-right'
import CkeDitor from './components/ckeDitor'

// 注册全局组件
Vue.component(Header.name, Header)
Vue.component(Buttom.name, Buttom)
Vue.component(Organization.name, Organization)
Vue.component(NavRight.name, NavRight)
Vue.component(CkeDitor.name, CkeDitor)

Vue.config.productionTip = false

import { reqTimes, reqNewList } from '@/api/index'
console.log(reqTimes());
console.log('132', reqNewList());



new Vue({
  render: h => h(App),
  beforeCreate() {
    // 全局事件总线 放在Vue的原型对象身上 这样所有的vc都可以访问到
    // 使这个全局事件总线的值为vm实例对象 这样全局事件总线就可以访问到vm的方法
    Vue.prototype.$bus = this // 安装全局事件总线 最标准写法
  },
  router,
  // 注册vuex 组件身上会多一个$store属性
  store
}).$mount('#app')
