// 对API进行统一管理
import requests from "./request";

export const reqNewList = () => {
  // 发请求
  return requests({
    url: '/ctcyc/api_v1/competition/hotNews',
    method: 'get'
  })
}

export const reqTimes = () => {
  return requests({
    url: '/ctcyc/api_v1/competition/info',
    method: 'get'
  })
}